@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
body {
  background: url(/static/media/gadgets.7e02b730.jpg) no-repeat center center fixed;
  background-size: cover;
}

.Forms-container {
  width: 300px;
  margin: 0 auto;
  margin-top: 40px;
}
.Forms-error {
  color: red;
}

.pagination {
  display: flex;
  list-style: none;
}

.Container {
  position: relative;

  height: 700px;
}

.Review-page-container {
  display: flex;

  justify-content: space-around;
  margin-top: 40px;
}

.Pagination-container {
  position: absolute;
  bottom: 0;
  left: 45%;
}

.review-card-container {
  width: 500px;
  min-height: 300px;
  height: 0%;
  background-color: #242424;
  color: #fff;
  border-radius: 20px;
  padding: 20px;
  font-size: 18px;
  line-height: 50px;
  box-shadow: inset 0 0 10px #000000;
}
.review-card-container-title {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
}

.Review-stars {
  display: flex;

  align-items: center;
}

.Navbar {
  height: 100px;
  background-color: #242424;
  padding: 20px;
  font-size: 28px;
  color: #fff;
}

